
import Left from "../components/Left";
import Rigth from "../components/Rigth";
import React,{  useState, useEffect} from 'react'
import Modal from "../components/Modal"
import jsonFile from "../renombre.json"
import { act } from "react-dom/test-utils";

const EPS = Object.keys(jsonFile);


const alerta = (data) => {
  alert(data.titulo + " " + data.body)
}

function Index() {

 
  const [data,setData] = useState({
    left: {},
    right: {},
    campos : [],
    
  })

  const [actual, setActual] = useState("")

  const [modal,setModal] = useState(false)


  useEffect(() => {
    
  },[data])

  useEffect(() => {  if(actual !== "") { setModal(true) } },[actual])

  const [loading, setLoading] = useState(false)

  return (
    


    <main className="container-md bg-slate-50 min-h-screen flex " >
    


    <Left data={data} setLoading={setLoading} setData={setData} jsonFile={jsonFile} EPSS={EPS} alerta={alerta} />

    
    <Rigth data={data} setData={setData} setLoading={setLoading} loading={loading} jsonFile={jsonFile} alerta={alerta} setActual={setActual} setModal={setModal} />


    {modal && <Modal data={data} setData={setData} setModal={setModal} setActual={setActual} actual={actual} />}
   

    </main>
  );
}

export default Index;
