import React, {useState,useEffect} from 'react'



export default function Modal({data,setData,setModal,setActual,actual}) {
    const [total,setTotal] = useState(1)
    const [temp,setTemp] = useState({})

    const nombres = data.right[actual].files.map((file) => file.name)

    const handleClick = (name) => {

        
        
        if( temp[name] ){

            setTemp({...temp,[name]:0})
            setTotal(total - 1)
        }

        else{
            setTemp({...temp,[name]: total })
            setTotal(total + 1)
        }
        
     
    }

    const handleUpdate = () => {

        

        const length = Object.keys(temp).length
     


        if(length === data.right[actual].files.length   ){
            const numOfZeros = Object.values(temp).filter((item) => item === 0).length
            if( numOfZeros === 0 ){

                const sortKeys = Object.keys(temp).sort((a,b) => temp[a] - temp[b])

                const newFiles = sortKeys.map((key) => data.right[actual].files.find((file) => file.name === key))

                setData({...data,right:{...data.right,[actual]:  {"files": newFiles,name:data.right[actual].name   }  }})
                setModal(false)

                
            }
            else{
                alert("No puedes dejar campos vacios")
            }
        }
        else{
            alert("No puedes dejar campos vacios")
        }

        
    }



  return (
     <div className='absolute h-screen w-screen  min-w-full min-h-full  bg-black bg-opacity-40 flex items-center justify-center '>

        <div className='h-5/6  w-5/6 bg-slate-200 p-5 rounded-xl flex flex-col items-center  overflow-y-scroll'>
            <h1 className='text-2xl font-semibold  text-center text-blue-800'>ORDENAR ARCHIVOS</h1>

            <section className='w-full flex  justify-center' >
                
                <article className='w-3/4 flex 
                 flex-wrap' >

                    {nombres.map((item,index) => {
                 
                        return (
                            <div className='w-2/4 flex flex-col items-center justify-center mb-5 cursor-pointer ' key={index}>


                                <h1 className='text-xl font-semibold text-blue-800 mb-2 whitespace-pre-wrap'>{item}</h1>


                                <div className='w-10 h-10 bg-blue-800 rounded-full text-center 
                                text-white text-2xl flex justify-center items-center' onClick={ ()=> handleClick(item) }>{ temp[item]  ? temp[item]  : ""}</div>
                            </div>
                        )
                    })}


                </article>


            </section>
            
            <button onClick={handleUpdate} className="w-1/6 h-10 bg-inherit border-green-600  border-2 mt-9  hover:bg-green-600 hover:text-white transition-all duration-300" >Guardar</button>
        
        </div>

     </div>
  )
}
